import React from 'react'
import Programs from './Programs'
import Notification from './Notification'
import { useMemo } from 'react'
import { Typography } from '@mui/material'

import Link from '@mui/material/Link'

const Results = ({
  state,
  county,
  tribe,
  showGeo,
  setShowGeo,
  geographic,
  setGeographic,
  tribal,
  setTribal,
  countySort,
  tribeSort,
}) => {
  console.log('Render Results')

  // useEffect(() => {
  //   if (tribe !== '' && state === '') {
  //     setShowGeo(false)
  //   } else {
  //     setShowGeo(true)
  //   }
  // }, [tribe, state, setShowGeo])

  const filterdGeo = useMemo(() => {
    return countySort()
  }, [county, state, geographic, countySort])

  const tribeResults = useMemo(() => {
    return tribeSort()
  }, [tribe, state, tribal, tribeSort])

  const resultsCount = useMemo(() => {
    if (tribe !== '' && state === '') {
      return tribeResults.length
    } else {
      return filterdGeo.length + tribeResults.length
    }
  }, [filterdGeo, tribeResults, tribe, state])

  console.log(resultsCount)

  return (
    <>
      <Notification
        resultsCount={resultsCount}
        myState={state}
        county={county}
        tribe={tribe}
        geographic={geographic}
        tribal={tribal}
      />

      <div>
        {showGeo &&
          filterdGeo.map((item) => {
            return <Programs key={item.index} {...item} />
          })}
      </div>
      <div>
        {tribeResults.map((item) => {
          return <Programs key={item.index} {...item} />
        })}
      </div>

      <div>
        <div>
          <Typography variant="h5">Tips to keep in mind</Typography>
          <ul>
            <li>
              <Typography display="inline" sx={{ fontWeight: 'bold' }}>
                Need help applying?&nbsp;
              </Typography>
              <Typography display="inline">Call </Typography>
              <Link href="tel:800-569-4287">(800) 569-4287</Link> or
              <Typography display="inline">&nbsp;</Typography>
              <Link href="https://www.consumerfinance.gov/find-a-housing-counselor/">
                find an approved housing counseling agency.
              </Link>
            </li>
            <li>
              <Typography display="inline" sx={{ fontWeight: 'bold' }}>
                Check back if you don't see a program that can help you right
                now.&nbsp;
              </Typography>

              <Typography display="inline">
                Some programs may only be closed temporarily while they process
                applications or until they get additional funds.
              </Typography>
            </li>
            <li>
              <Typography display="inline" sx={{ fontWeight: 'bold' }}>
                After applying it may take several weeks to receive rental
                assistance funds.&nbsp;
              </Typography>

              <Typography display="inline">
                <Link href="https://www.consumerfinance.gov/coronavirus/mortgage-and-housing-assistance/renter-protections/what-to-do-if-youre-facing-eviction/">
                  If you're worried about eviction, find out what to do next.
                </Link>
              </Typography>
            </li>
          </ul>
          <Typography variant="h6">
            Additional help for your situation
          </Typography>
          <ul>
            <li>
              <Typography>
                <Link href="https://www.consumerfinance.gov/coronavirus/mortgage-and-housing-assistance/renter-protections/">
                  Help for renters
                </Link>
              </Typography>
            </li>
            <li>
              <Typography>
                <Link href="https://www.consumerfinance.gov/coronavirus/mortgage-and-housing-assistance/help-for-landlords/">
                  Help for landlords
                </Link>
              </Typography>
            </li>
          </ul>
        </div>
      </div>
    </>
  )
}

export default Results
