export const getCounties = (state, countyOptions) => {
  if (state) {
    const result = Object.keys(countyOptions)
      .filter((key) => key.includes(state))
      .reduce((cur, key) => {
        return Object.assign(cur, { [key]: countyOptions[key] })
      }, {})

    const counties = result[Object.keys(result)[0]]

    if (typeof counties === 'undefined') {
      return []
    } else {
      const m = counties.map((county) => {
        return { value: county, label: county }
      })
      return m
    }
  }
}

export function hasKeySetTo(obj, key, value) {
  return obj.hasOwnProperty(key) && obj[key] === value
}

export const showCounty = (state, geographic) => {
  return geographic
    .filter((result) => {
      return result.state === state
    })
    .filter((item) => {
      return hasKeySetTo(item, 'type', 'County')
    })
}
