import React from 'react'
import { useReducer, useEffect, useMemo } from 'react'
import reducer from '../reducer/index'
import Links from './Links'
import { Typography } from '@mui/material'
import Alert from '@mui/material/Alert'

const Notification = ({
  resultsCount,
  myState,
  county,
  tribe,
  geographic,
  tribal,
}) => {
  console.log('Render Notification')
  const [state, dispatch] = useReducer(reducer, {
    explanation: '',
    links: null,
    message: `Showing ${resultsCount} total rental assistance programs.`,
    type: 'info',
    count: resultsCount,
  })

  const defaultLength = useMemo(() => {
    return geographic.length + tribal.length
  }, [geographic, tribal])

  useEffect(() => {
    switch (true) {
      case myState === undefined:
        return dispatch({
          type: 'none',
          payload: {
            explanation:
              'Housing counselors can help you find resources in your area and make a plan.',
            links: [
              {
                text: 'Find a housing counselor',
                url:
                  'https://www.consumerfinance.gov/find-a-housing-counselor/',
              },
              {
                text: 'Get advice about your legal rights',
                url:
                  'https://www.consumerfinance.gov/find-a-housing-counselor/',
              },
            ],
            message: 'Invalid Zip Code',
            type: 'error',
          },
        })
      case resultsCount === 1:
        return dispatch({
          type: 'one',
          payload: {
            explanation:
              'We found a program that may provide rental assistance to your area.',
            message: '1 program found.',
            type: 'success',
            links: null,
          },
        })
      case resultsCount === defaultLength:
        return dispatch({
          type: 'many',
          payload: {
            count: 0,
            explanation: '',
            message: `Showing ${resultsCount} programs that may serve your area.`,
            type: 'info',
            links: null,
          },
        })
      case resultsCount > 1:
        return dispatch({
          type: 'many',
          payload: {
            count: resultsCount,
            explanation:
              'To see if you qualify, start with the most local program first.',
            message: `Showing ${resultsCount} programs that may serve your area.`,
            type: 'success',
            links: null,
          },
        })
      case resultsCount === 0:
        return dispatch({
          type: 'none',
          payload: {
            explanation:
              'Housing counselors can help you find resources in your area and make a plan.',
            links: [
              {
                text: 'Find a housing counselor',
                url:
                  'https://www.consumerfinance.gov/find-a-housing-counselor/',
              },
              {
                text: 'Get advice about your legal rights',
                url:
                  'https://www.consumerfinance.gov/find-a-housing-counselor/',
              },
            ],
            message:
              'Sorry, we did not find a rental assistance program for your area.',
            type: 'warning',
          },
        })
      case resultsCount === 1:
        return dispatch({
          type: 'one',
          payload: {
            explanation:
              'We found a program that may provide rental assistance to your area.',
            message: '1 program found.',
            type: 'success',
            links: null,
          },
        })
      case resultsCount === defaultLength:
        return dispatch({
          type: 'many',
          payload: {
            count: 0,
            explanation: '',
            message: `Showing ${resultsCount} programs that may serve your area.`,
            type: 'info',
            links: null,
          },
        })
      case resultsCount > 1:
        return dispatch({
          type: 'many',
          payload: {
            count: resultsCount,
            explanation:
              'To see if you qualify, start with the most local program first.',
            message: `Showing ${resultsCount} programs that may serve your area.`,
            type: 'success',
            links: null,
          },
        })

      default:
        return dispatch({
          type: 'default',
          payload: {
            explanation: '',
            links: null,
            message: 'Showing 513 total rental assistance programs.',
            type: 'info',
            count: 0,
          },
        })
    }
  }, [resultsCount])

  return (
    <Alert severity={state.type} sx={{ my: 4 }}>
      <Typography variant="h5">{state.message}</Typography>

      <Typography variant="subtitle1">{state.explanation}</Typography>

      {state.links && <Links state={state} />}
    </Alert>
  )
}

export default Notification
