import React, { useCallback } from 'react'
import { useState, useMemo } from 'react'
import SelectOptions from './SelectOptions'
import Results from './Results'
import Zip from './Zip'

import data from '../data/geographic'
import tribalx from '../data/tribal'
import stateOptions from '../data/StateOptions'
import countyD from '../data/countyData'

import { getCounties } from '../helpers'
import { showCounty } from '../helpers'

import { Typography } from '@mui/material'

import Divider from '@mui/material/Divider'
import Container from '@mui/material/Container'

function App() {
  console.log('Render App')

  const [state, setState] = useState('')
  const [county, setCounty] = useState('')
  const [tribe, setTribe] = useState('')

  const [showGeo, setshowGeo] = useState(true)

  const [geographic, setGeographic] = useState(data)
  const [tribal, setTribal] = useState(tribalx)

  console.log(state, county, tribe)

  const sortOrder = ['City', 'County', 'State']

  const stateSort = useCallback(() => {
    if (state === '') {
      return geographic
    }
    return geographic
      .filter((item) => {
        return item.state === state
      })
      .sort(function(a, b) {
        return sortOrder.indexOf(a.type) - sortOrder.indexOf(b.type)
      })
  }, [state, geographic])

  const countySort = useCallback(() => {
    if (county === '') {
      return stateSort()
    }
    return geographic
      .filter((item) => {
        return (
          item.name.includes(county + ' County') ||
          (item.county !== undefined &&
            item.county.includes(county + ' County')) ||
          (item.type === 'State' && item.state === state)
        )
      })
      .sort(function(a, b) {
        return sortOrder.indexOf(a.type) - sortOrder.indexOf(b.type)
      })
  }, [county, state, geographic, stateSort])

  const tribeSort = useCallback(() => {
    if (tribe === '' && state === '') {
      return tribal
    }
    return tribal.filter((item) => {
      return item.name === tribe
    })
  }, [tribe, state, tribal])

  const options = useMemo(() => {
    return stateOptions.map((option) => {
      return { value: option, label: option }
    })
  }, [stateOptions])

  const countyOptions = useMemo(() => {
    return getCounties(state, countyD)
  }, [countyD, state])

  const tribeOptions = useMemo(() => {
    return tribal.map((options) => {
      return { value: options.name, label: options.name }
    })
  }, [tribal])

  const handleChange = (e, action, name) => {
    setState(e === null ? '' : e.value)
  }

  const handleTribeChange = (e, action) => {
    setTribe(e === null ? '' : e.value)
  }

  const handleCountyChange = (e, action) => {
    setCounty(e === null ? '' : e.value)
  }

  return (
    <Container>
      <div className="k-container">
        <div className="rental-assistance-finder">
          <div className="filters o-well block block__sub">
            <Typography variant="h4" component="div" sx={{ mt: 4 }}>
              Rental Assistance Programs In Your Area
            </Typography>
            <Divider sx={{ my: 4 }} />
            <Zip setState={setState} setCounty={setCounty} />
            <SelectOptions
              className="state-select-container"
              id="state-select"
              label="For your state or territory"
              placeholder="Select your state or territory"
              options={options}
              onChange={handleChange}
              name="State"
            />
            {showCounty(state, geographic).length > 0 && (
              <SelectOptions
                className="county-select-container"
                id="county-select"
                label="Narrow results by county (optional)"
                placeholder="Select your county"
                onChange={handleCountyChange}
                options={countyOptions}
              />
            )}
            <SelectOptions
              className="tribe-select-container"
              id="tribe-select"
              label="For your tribe or the tribal lands where you live (if applicable)"
              placeholder="Select your tribe or tribal lands"
              helperText="Only tribes with rental assistance programs are listed."
              options={tribeOptions}
              onChange={handleTribeChange}
            />
          </div>
        </div>

        <Results
          state={state}
          county={county}
          tribe={tribe}
          showGeo={showGeo}
          setShowGeo={setshowGeo}
          geographic={geographic}
          setGeographic={setGeographic}
          tribal={tribal}
          setTribal={setTribal}
          countySort={countySort}
          tribeSort={tribeSort}
        />
      </div>
    </Container>
  )
}

export default App
