const reducer = (state, action) => {
  switch (action.type) {
    case 'none':
      return {
        ...state,
        ...action.payload,
      }
    case 'one':
      return {
        ...state,
        ...action.payload,
      }
    case 'many':
      return {
        ...state,
        ...action.payload,
      }
    default:
      return {
        ...state,
        explanation: '',
        message: `Showing ${state.count} total rental assistance programs.`,
      }
  }
}

export default reducer
