import React from 'react'
import axios from '../api/index.js'
import { useState, useEffect } from 'react'
import { useSearchParams } from 'react-router-dom'

const Zip = ({ setState, setCounty }) => {
  const [zipcode, setZipcode] = useState('')
  const [searchParams, setSearchParams] = useSearchParams('')

  const zip = searchParams.get('zipcode')

  let getCityStateCounty = async (zip) => {
    try {
      let response = await axios.get(`/USCities?zipcode=${zip}`)
      let data = response.data

      if (response.status === 200) {
        console.log('good reponse', data.county, data.state)
        setCounty(data.county === undefined ? '' : data.county)
        setState(data.state === undefined ? '' : data.state)
      }
    } catch (error) {
      console.log('Zip Code not valid')
      setState(undefined)
      console.log('error', error.response) // this is the main part. Use the response property from the error object
      return error.response
    }
  }

  useEffect(() => {
    getCityStateCounty(zip)
  }, [])

  let handleSubmit = async (e) => {
    e.preventDefault()
    getCityStateCounty(zipcode)
  }

  const handleChange = (e) => {
    const { value } = e.target
    setZipcode(value.replace(/[^\d{5}]$/, '').substr(0, 5))
  }
  return (
    <>
      {/* <form onSubmit={handleSubmit} className="form-data">
        <label htmlFor="zip">Type Zip Code Here</label>
        <input
          className="zip"
          value={zipcode}
          placeholder=""
          type="text"
          name="zip"
          id="zip"
          onChange={handleChange}
        />
      </form> */}
    </>
  )
}

export default Zip
